import React, { useState } from "react";
import { Button, Form } from "react-bootstrap";
import axios from "axios";

import "./forget-password-page2.scss";
import { Toast } from "./../../toaster/toaser";

const ForgetPasswordPage2 = ({ email, setIsRightCode, history: { push } }) => {
  const [code, setCode] = useState("");

  const handleConfirmCode = (e) => {
    e.preventDefault();
    axios
      .post("/checkResettingCode", { email, code })
      .then(({ data: res }) => {
        res
          ? (() => {
              setIsRightCode(res);
              push("/account/forgetpassword/3");
            })()
          : Toast("الرمز خاطئ ، الرجاء ادخال الرمز الصحيح", false);
        // res ?push({pathname:"/",state:{id}})
      })
      .catch((e) => {
        Toast("حدث خطأ ما ، الرجاء المحاولة مرة اخرى", false);
      });
  };

  return (
    <Form onSubmit={handleConfirmCode} className="forget-password-page2-form">
      <span>تم ارسال رمز الى البريد الالكتروني الرجاء كتابته</span>
      <Form.Group controlId="formBasicEmail">
        <Form.Control
          className="inputs-style"
          type="number"
          placeholder="الرمز"
          onChange={({ target: { value } }) => setCode(value)}
          required
          autoComplete="off"
        />
      </Form.Group>

      <Button
        variant="primary"
        type="submit"
        className="forget-password-page2-button"
      >
        تعيين كلمة المرور الجديد
      </Button>
    </Form>
  );
};

export default ForgetPasswordPage2;
